<template>
    <div class="myWallet">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">我的钱包</span>
            <span class="back"></span>
        </div>
        <div class="fixedArea">
          <div class="myMoney">
            <div class="content">
              <div class="money">
                <img src="../../assets/img/balance.png" @click="showBalance">
              </div>
              <div class="balance">账户余额</div>
              <div class="balanceNum">￥{{ myMoney[0] }}</div>
            </div>
            <div class="content">
              <div class="money">
                <img src="../../assets/img/money.png" @click="showBalance">
              </div>
              <div class="balance">哼哈值</div>
              <div class="balanceNum">{{ myMoney[1] }}</div>
            </div>
          </div>
        <div class="recharge">
            <mt-button class="rechargeBtn out">提现</mt-button>
            <mt-button class="rechargeBtn in" @click="reCharge">充值</mt-button>
        </div>
        <div class="detailHeader">交易明细</div>
        </div>
        <div class="transactionDetails">
            <ul class="details" v-if="showCash">
                <li class="detail" v-for="(cashTransaction, index) in cashTransactions" :key="index">
                    <span class="detailLeft">
                        <span class="transactionType">{{ cashTransaction.fFlowType }}</span>
                        <span class="transactionTime">{{ cashTransaction.fTradeTime }}</span>
                    </span>
                    <span class="detailRight" :class="cashTransaction.inandout === 'To reduce' ? normal : orange">{{ cashTransaction.fFlowVal }}</span>
                </li>
            </ul>
            <ul class="details" v-if="!showCash">
                <li class="detail" v-for="(hhTransaction, index) in hhTransactions" :key="index">
                    <span class="detailLeft">
                        <span class="transactionType">{{ hhTransaction.fFlowType }}</span>
                        <span class="transactionTime">{{ hhTransaction.fTradeTime }}</span>
                    </span>
                    <span class="detailRight" :class="hhTransaction.inandout === 'To reduce' ? normal : orange">{{ hhTransaction.fFlowVal }}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { getMyWallet } from '../../api/getdata'
export default {
  name: 'myWallet',
  data () {
    return {
      myMoney: '',
      orange: 'orange',
      normal: 'normal',
      cashTransactions: [],
      hhTransactions: [],
      showCash: true
    }
  },
  mounted () {
    this.getMyWallet()
    var ws = new WebSocket('wss://henghasports.com/ws')
    ws.onopen = function () {
      // ws.send('hello')
      console.log('建立远程连接成功')
      // console.log(ws.readyState)
    }
    ws.onclose = function () {
      console.log('断开连接')
    }
    ws.onmessage = function (e) {
      var data = e.data
      console.log(data)
      if (data === 'success') {
        this.getMyWallet()
      }
    }
  },
  methods: {
    back () {
      this.$router.push('/home/mine')
    },
    getMyWallet () {
      getMyWallet().then(res => {
        // console.log(res.data)
        this.myMoney = res.data
        this.cashTransactions = res.data[2].reverse()
        this.hhTransactions = res.data[3].reverse()
      })
    },
    reCharge () {
      this.$router.push('/home/mine/recharge')
    },
    showBalance () {
      this.showCash = !this.showCash
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    font-size: 0.48rem;
    background: #ffffff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .content {
    margin-top: 0.37333rem;
    width: 49%;
  }

  .money {
    height: 1.30667rem;
    margin: 0 auto;
  }

  .money img {
    width: auto;
    height: 100%!important;
  }

  .balance,
  .balanceNum {
    width: 100%;
    text-align: center;
  }

  .balance {
    font-size: 0.34667rem;
    color: #666666;
    margin: 0.2rem 0 0.1rem 0;
  }

  .balanceNum {
    font-size: 0.48rem;
    color: #333333;
  }

  .recharge {
    border-bottom: 0.32rem solid #f5f5f5;
    padding-bottom: 0.53333rem;
    margin-top: 0.48rem;
  }

  .rechargeBtn {
    width: 3.54667rem;
    height: 1.04rem;
  }

  .out {
    border: 1px solid #ffd200;
    background: #fff;
    color: #333333;
  }

  .in {
    background: #ffd200;
    color: #ffffff;
    border: none;
  }

  .details {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .detailHeader {
    text-align: start;
    padding: 0.37333rem 0 0.37333rem 0.58667rem;
    color: #333333;
  }
  .detail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f5f5f5;
    padding: 0.34667rem 0;
  }

  .detailLeft span {
    display: flex;
    justify-content: start;
  }

  .transactionType {
    color: #333333;
    font-size: 0.4rem;
  }

  .transactionTime {
    color: #666666;
    font-size: 0.34667rem;
  }

  .normal {
    color: #333333;
  }

  .orange {
    color: #ff8309;
  }

  .myMoney {
    display: flex;
    justify-content: space-between;
  }

  .fixedArea {
    width: 100%;
    position: fixed;
    top: 1.5rem;
    left: 0;
    background: #ffffff;
  }

  .transactionDetails {
    margin-top: 8rem;
  }
</style>



